<script>
import loadXMLDoc from '@/store/AjaxRequest';
// import loadXMLDoc from '@/store/AjaxRequest.js';
export default {
    data() {
        return {
            cardarr: null
        }
    },
    created() {
        loadXMLDoc({
            reqBody: {
                'token': localStorage.getItem('token'),
                'deviceid': localStorage.getItem("equipment"),
                "name": "54188",
            },
            reqUrl: "http://8.148.31.153:3030/queryCertificate",
            reqHeader: { 'Content-Type': 'application/json' },
            callback: (error, resdata) => {
                try {
                    if (!error) {
                        const data = JSON.parse(resdata);
                        this.cardarr = data.resData.certificate
                        localStorage.setItem("userCardArr", resdata)
                    }
                } catch (error) {
                    console.log(error)
                }
            }
        })
    },
    methods: {
        // PDF下载
        getPDF() {
            function downloadFile({
                reqBody = {},
                reqMethod = "post",
                reqUrl = "",
                async = true,
                reqHeader = { "Content-Type": "application/x-www-form-urlencoded" },
                callback
            }) {
                let params = null;
                let reqBodyData = null;
                switch (Object.values(reqHeader)[0]) {
                    case "application/x-www-form-urlencoded":
                        params = new URLSearchParams();
                        Object.entries(reqBody).forEach(([key, value]) => {
                            params.append(key, value);
                        });
                        reqBodyData = params.toString();
                        break;
                    case "application/json":
                        params = JSON.stringify(reqBody);
                        reqBodyData = params;
                        break;
                }
                const xhr = new XMLHttpRequest();
                xhr.open(reqMethod, reqUrl, async);
                Object.entries(reqHeader).forEach(([key, value]) => {
                    xhr.setRequestHeader(key, value);
                });
                xhr.responseType = 'blob';
                xhr.onload = function () {
                    if (xhr.status >= 200 && xhr.status < 300) {
                        const contentDisposition = xhr.getResponseHeader('Content-Disposition');
                        let filename = 'download.pdf'; // 默认文件名
                        if (contentDisposition) {
                            const filenameMatch = contentDisposition.match(/filename="?(.+)"?/);
                            if (filenameMatch && filenameMatch[1]) {
                                filename = filenameMatch[1];
                            }
                        }
                        const blob = new Blob([xhr.response], { type: xhr.getResponseHeader('Content-Type') });
                        const url = window.URL.createObjectURL(blob);
                        const a = document.createElement('a');
                        a.href = url;
                        a.download = filename;
                        document.body.appendChild(a);
                        a.click();
                        setTimeout(() => {
                            document.body.removeChild(a);
                            window.URL.revokeObjectURL(url);
                            callback(null, { success: true, filename });
                        }, 100);
                    } else {
                        callback(new Error(`请求失败，状态码：${xhr.status}`));
                    }
                };
                xhr.onerror = function () {
                    callback(new Error("网络错误"));
                };
                xhr.send(reqBodyData);
            }
            downloadFile({
                reqUrl: 'http://8.148.31.153:3030/downloadCertificate',
                reqHeader: {
                    "Content-Type": "application/json",
                    "Authorization": "Bearer your-token"
                },
                callback: (err, result) => {
                    if (err) {
                        console.error('下载失败:', err);
                    } else {
                        console.log('下载成功:', result.filename);
                    }
                }
            });
        },
    }
};
</script>

